@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@300;400;600&display=swap");
:root {
  --primary-blue: hsl(215, 51%, 70%);
  --primary-cyan: hsl(178, 100%, 50%);
  --main-bg: hsl(217, 54%, 11%);
  --card-bg: hsl(216, 50%, 16%);
  --line: hsl(215, 32%, 27%);
  --white: hsl(0, 0%, 100%);
}

body {
  font-family: "Outfit", sans-serif;
}

.card .content h1 {
  font-size: 1.5rem;
  margin: 0.8em 0;
}
.card .content p {
  font-size: 1.125rem;
  color: var(--primary-blue);
  font-weight: 300;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  display: grid;
  place-items: center;
  min-height: 100vh;
  background: var(--main-bg);
  color: var(--white);
}

.card {
  max-width: 21.875rem;
  background: var(--card-bg);
  border-radius: 0.9375rem;
  padding: 1.5em;
  overflow: hidden;
  box-shadow: 0 25px 20px rgba(0, 0, 0, 0.2);
}
.card .image img {
  max-width: 100%;
  border-radius: 0.625rem;
}
.card .content .value {
  margin: 1.5em 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.card .content .value span {
  display: flex;
  align-items: center;
}
.card .content .value span img {
  margin-right: 0.5em;
}
.card .content .value span:first-child {
  color: var(--primary-cyan);
  font-weight: 600;
}
.card .content .value span:last-child {
  color: var(--primary-blue);
  font-weight: 300;
}
.card hr.line {
  border: 1px solid var(--line);
}
.card .footer {
  display: flex;
  align-items: center;
  margin: 1em 0;
}
.card .footer span {
  color: var(--primary-blue);
  margin-right: 0.3em;
}
.card .footer img {
  border-radius: 50%;
  border: 1px solid var(--white);
  max-width: 1.875rem;
  margin-right: 1em;
}